// third-party
import { createSlice } from '@reduxjs/toolkit';

// project imports
import axios from 'utils/axios';
import { dispatch } from '../index';

// ----------------------------------------------------------------------

const initialState = {
    error: null,
    interimInvoiceData: [],
    savestate : false
};

const slice = createSlice({
    name: 'inventory',
    initialState,
    reducers: {
        hasError(state, action) {
            state.error = action.payload;
        },
        // EDIT ADDRESS
        SaveInterimInvoiceSuccess(state, action) {
            state.interimInvoiceData = action.payload;
            state.savestate = true;
        }
    }
});

// Reducer
export default slice.reducer;

// ----------------------------------------------------------------------

export function SaveInterimInvoice(AllData) {
    return async () => {
        try {
            dispatch(slice.actions.SaveInterimInvoiceSuccess(AllData));
        } catch (error) {
            dispatch(slice.actions.hasError(error));
        }
    };
}
