// material-ui
import { useTheme } from '@mui/material/styles';
import {
    Typography , Grid
} from '@mui/material';
import Erpicon from './erp-system-icon-8.jpg';
/**
 * if you want to use image instead of <svg> uncomment following.
 *
 * import logoDark from 'assets/images/logo-dark.svg';
 * import logo from 'assets/images/logo.svg';
 *
 */

// ==============================|| LOGO SVG ||============================== //

const Logo = () => {
    const theme = useTheme();

    return (
        /**
         * if you want to use image instead of svg uncomment following, and comment out <svg> element.
         *
         * <img src={theme.palette.mode === 'dark' ? logoDark : logo} alt="Berry" width="100" />
         *
         */
        <>
            <Grid container alignItems="center" spacing={2}>
                <Grid item>
                    <img src={Erpicon} alt="Image" style={{ width: '50px', height: '50px' , color : "#1e88e5" }} />
                </Grid>
                <Grid item>
                    <Typography variant="h4">ERP SYSTEM</Typography>
                </Grid>
            </Grid>
        </>
    );
};

export default Logo;
